.main_header{
    width: 100vw;
    height: cal(100vh - 3rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;
}
.main_header_container{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    gap: 6rem;
}
.main_header-left{
    margin-top: 4rem;
}
.main_header-left h4{
    margin-bottom: 1rem;
    color: var(--color-secondary);
    font-weight: 400;
}
.main_header-left p{
    margin: 1rem 0 3.5rem;
    font-size: 1.1rem;
}

/* main right side */

.main_header-right{
    display: grid;
    place-items: center;
    position: relative;
    margin-top: 8rem;
}
.main_header-circle{
    width: 25rem;
    height: 25rem;
    background: linear-gradient(75deg, var(--color-secondary), transparent);
    border-radius: 50%;
    position: absolute;
    filter: blur(15px);
    transition: var(--transition);
}
.main_header-image{
    position: relative;
}

/* program section */

section{
    margin-top: 10rem;
}
.section_head{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.section_head span{
    background: var(--color-gray-500);
    padding: 0.8rem;
    border-radius: 1rem;
    color: var(--color-secondary);
    font-size: var(1.5rem);
}


.card{
    background: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 0 3rem 0 3rem;
    transition: var(--transition);
}
.card:hover{
    background: transparent;
    border-color: var(--color-gray-400);
    cursor: default;
}
.card span{
    width: 3rem;
    height: 3rem;
    background: var(--color-primary);
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: var(--transition);
}
.card:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.card small{
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}

/* ***************************** PROGRMAS *************************** */

.progrmas{
    margin-top: 4rem;
}
.programs_wrapper{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin-top: 4rem;
}
.programs_program a{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.programs_program:hover a{
    background: var(--color-secondary);
    columns: var(--color-gray-600); 
}


/* VALUES */

.values_container{
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%;
}
.values-image{
    filter: saturate(0.25);
    transition: var(--transition);
}
.values-image:hover{
    filter: saturate(1);
}
.values-right > p{
    margin: 1.5rem 0 5rem;
}
.values_wrapper{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 3.4rem 2.4rem;
}
.card.values_value{
    padding-top: 3rem 3.5rem;
    text-align: left;
    position: relative;
}
.card.values_value span{
    position: absolute;
    top: -1.5rem;
}

/* FAQs Section */

.faqs_wrapper{
    margin-top: 4rem;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5rem 5rem;
    align-items: flex-start;
}
.faq{
    background: var(--color-gray-500);
    padding: 2rem;
    border-radius: 1rem;
    cursor: pointer;
}
.faq div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.faq_icon{
    background: transparent;
    color: var(--color-gray-100);
    font-size: 1.5rem;
}
.faq p{
    margin-top: 1.5rem;
}

/* TESTIMONIALS    */

.testimonials{
    width: 50rem;
    margin-inline: auto;
}
.testimonials_head{
    justify-content: center;
}
.card.testimonial{
    position: relative;
    text-align: left;
    margin-top: 5rem;
}
.testimonial_avtar{
    width: 4rem;
    height: 4rem;
    bottom: 16.6rem;
    border-radius: 1.5rem; 
    border: 2px solid transparent;
    overflow: hidden;
    position: absolute;
    left: -2rem;
    left: calc(50% - 2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
    transition: var(--transition);
}
.testimonials:hover .testimonial_avtar{
    border-radius: 50%;
    border-color: var(--color-gray-400);
}
.testimonial_quote{
    font-style: italic;
    margin: 1rem 0 2rem;
}
.card small.testimonial_title{
    margin-top: 0.3rem;
}
.testimonial_btn-container{
    width: fit-content;
    margin: 2.5rem auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem   ;
}
.testimonial_btn{
    background: transparent;
    cursor: pointer;
}
.testimonial_btn svg{
    fill: var(--color-primary);
    font-size: 1.8rem;
    width: 3rem;
    height: 3rem;
}

/* Footer Section */

footer{
    background: var(--color-primary);
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: var(--color-gray-100);
}
.footer_container{
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;
}
.footer_container article{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
}
.footer_container article p{
    margin-top: 0.5rem;
}
.footer_container article h4{
    margin-bottom: 0.6rem;
    font-size: 1rem;
}
.footer_socials{
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.footer_socials a{
    background: var(--color-gray-100);
    padding: 0.7rem;
    border: 1px solid transparent;
    border-radius: 0.6rem;
    transition: var(--transition);
}
.footer_socials a svg{
    color: var(--color-gray-600);
}
.footer_socials a:hover{
    border-color: var(--color-gray-100);
    background: transparent;
}
.footer_copyright{
    color: var(--color-gray-100);
    text-align: center;
    padding: 1.5rem 0;
    border-top: 2px solid var(--color-primary-variant);
    margin-top: 5rem;
}

/* media query */

@media screen and (max-width: 1024px){
    /* main header */
    .main_header{
        height: fit-content;
        padding: 12rem 0;
    }
    .main_header-left{
        margin-top: 10rem;
    }
    .main_header_container{
        gap: 0;
    }
    .main_header-circle{
        width: 16rem;
        height: 16rem;
    }


    /* programs */
   .programs_wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr;
   }
   /* Values */
   .values_container{
    grid-template-columns: 1fr;
    justify-content: center;
    gap: 4rem;
   }
   .values_wrapper{
    gap: 4rem 3rem;
   }
   .values-image{
    display: none;
   }

   /* FAQs
    */
    .faqs_wrapper{
        grid-template-columns: 1fr;
    }


    /* Testimonials */
    .testimonials{
        width: 75%;
    }
    .testimonial_avtar{
        top: -2rem;
    }

    /* Footer */

    .footer_container{
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}



/* media queries for small devices */
@media screen and (max-width: 600px){
    /* main header */
    .main_header{
        margin-top: 0;
        padding: 12rem 0 0 ;
        height: 100vh;
        margin-bottom: 2rem;
    }
    .main_header-left{
        margin-top: 10rem;
    }
    .main_header-image, .main_header-circle{
        display: none;
    }
    .main_header_container{
        grid-template-columns: 1fr;
    }

    /* Programs */

    .programs_wrapper{
        grid-template-columns: 1fr;
        gap: 2rem;
        margin-top: 3rem;
    }
    .programs_program{
        width: 84%;
        margin: 0 auto;
    }

    /* Values */

    .values_wrapper{
        grid-template-columns: 1fr;
        grid-area: 3rem;
    }
    .card.card.values_value{
        width: 84%;
        margin-inline: auto;
    }

    /* FAQs */

    .faqs_wrapper{
        gap: 1rem;
        margin-top: 3rem;
    }

    /* Testmonials */
    .testimonials{
        width: 100%;
    }
    .testimonial_avtar{
        top: -2rem;
    }

    /* Footer */
    footer{
        margin-top: 7rem;
    }
    .footer_container{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .footer_container article{
        align-items: center;
    }
    .footer_container article p{
        text-align: center;
    }
}